import CommentService from '../services/CommentService';

export function loadComments() {
  return async dispatch => {
    try {
      const comments = await CommentService.query();
      dispatch(setComments(comments));
    } catch (err) {
      console.log('CommentActions: err in loadComments', err);
    }
  };
}
export function addComment(commentObj, postId) {
  // console.log('commentObj: ' + commentObj)
  return async dispatch => {
    try {
      const addedComment = await CommentService.add(commentObj, postId);
      console.log('comment added: ' + commentObj.txt + ' to post: ' + postId)
      dispatch(_addComment(addedComment));
    } catch (err) {
      console.log('CommentActions: err in addComment', err);
    }
  };
}
function setComments(comments) {
  return {
    type: 'SET_COMMENTS',
    comments
  };
}
function _addComment(comment) {
  return {
    type: 'COMMENT_ADD',
    comment
  };
}
