import React, { Component } from 'react';
import { connect } from 'react-redux';
import '../assets/css/LoggedinUserPage/loggedinUserPage.css'
import { loadPosts } from '../actions/PostActions.js';
import { loadUsers } from '../actions/UserActions.js';

class LoggedInUserPage extends Component {
    state = {
        postsCount: 0,
        followersCount: 0,
    }
    componentDidMount() {
        this.props.loadUsers()
        this.props.loadPosts()
        this.countPosts(this.props.posts)
        this.countFollowers(this.props.users, this.props.loggedInUser)
    }

    countPosts = (posts) => {
        let postsCount = 0
        posts.forEach(post => {
            if (post.owner._id === this.props.loggedInUser._id) {
                console.log('post: ', post)
                postsCount++
            }
        });

        let result = ''
        if (postsCount > 1) {
            result = postsCount + ' posts';
        } else if (postsCount === 0) {
            result = 'no posts'
        } else {
            result = '1 post';
        }
        this.setState({ postsCount: result })
    }
    countFollowers = (users, selectedFriend) => {
        let followersCount = 0
        users.forEach((user) => {
            if (user.following) {
                user.following.forEach((followedUser) => {
                    if (selectedFriend._id === followedUser._id) {
                        // console.log('Match! ' + ' selectedFriend._id: ' + selectedFriend._id + ' followedUser: ' + followedUser._id)
                        followersCount++
                    }
                })
            }
        });
        let result = ''
        if (followersCount > 1) {
            result = followersCount + ' followers';
        } else if (followersCount === 0) { result = 'no followers' }
        else { result = '1 follower'; }
        this.setState({ followersCount: result })
    }

    render() {
        const { loggedInUser } = this.props
        return (<div className="user-page-container">

            <div className="user-info flex col">
                <div className="image-username-container flex row">
                    <img className="user-img" alt="user pic" src={loggedInUser.imgUrl}></img>
                    <div className="user-socual-info-container flex col">
                        <span className="username">{loggedInUser.username}</span>
                        <div className="socual-info-section flex row">
                            <span className="user-info-field username">{this.state.postsCount}</span>
                            <span className="user-info-field username">{this.state.followersCount}</span>
                            <span className="user-info-field username">folowing: </span>
                        </div>
                    </div>
                </div>

            </div>

        </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        loggedInUser: state.user.loggedInUser,
        posts: state.post.posts,
        users: state.user.users
    };
};
const mapDispatchToProps = {
    loadUsers, loadPosts
};

export default connect(mapStateToProps, mapDispatchToProps)(LoggedInUserPage);