import React, { Component } from 'react';
import { connect } from 'react-redux';
import SocketService from '../services/SocketService';
import { loadPosts, loadPost, likePost, addPost, removePost } from '../actions/PostActions';
import { loadUsers } from '../actions/UserActions';
import { addComment, loadComments } from '../actions/CommentActions';

import '../assets/css/PostPage/PostPage.css'
class PostPage extends Component {
    state = {
        comment: {
            _id: '',
            txt: '',
            owner: {
                _id: this.props.location.state.post.owner._id,
                nickName: this.props.location.state.post.owner.nickName,
                imgUrl: this.props.location.state.post.owner.imgUrl
            }
        },
        comments: [],
        post: this.props.location.state.post
    };

    componentDidMount() {
        // console.log(JSON.stringify(this.state.comment))
        this.props.loadUsers()
        SocketService.setup();
        // SocketService.emit('user post', this.state.topic);
        SocketService.on('post addComment', this.addComment);
        // this.checkStufff()
    }
    componentWillUnmount() {
        SocketService.off('post addComment', this.addComment);
        SocketService.terminate();
    }
    checkStufff = () => {
        // this.setState({ post: this.props.location.state.post })

        // this.setState({ comment: { nickName: this.state.post.owner.nickName } });
        // this.setState(prevState => ({
        //     comment: {
        //         ...prevState.comment,
        //         nickName: this.state.post.owner.nickName
        //     }
        // }));

        // console.log('nickName: ' + this.state.comment.nickName)
    }

    addComment = (newComment) => {
        this.setState(prevState => ({ comments: [...prevState.comments, newComment] }));
    };
    changeTopic = () => {
        SocketService.emit('chat topic', this.state.topic);
    };
    sendComment = ev => {
        ev.preventDefault();
        SocketService.emit('post newComment', this.state.comment.txt);
        // console.log('this.state.comment: ' + JSON.stringify(this.state.comment))

        var date = new Date();
        var timestamp = date.getTime();

        var commentObj = {
            _id: '',
            createdAt: timestamp,
            owner: {
                _id: this.props.loggedInUser._id,
                nickName: this.props.loggedInUser.username,
                imgUrl: this.props.loggedInUser.imgUrl
            },
            txt: this.state.comment.txt
        }
        // console.log('commentObj: ' + JSON.stringify(commentObj))
        // console.log('commentObj: ' + commentObj)
        this.props.addComment(commentObj, this.state.post._id)

        this.setState({ comment: { txt: '' } });//nickName: this.props.location.state.post.owner.nickName, 
    };
    handleChange = ev => {
        const { name, value } = ev.target;
        this.setState({ [name]: value }, () => this.changeTopic(value));
    };
    commentHandleChange = ev => {
        const { name, value } = ev.target;
        this.setState(prevState => {
            return {
                comment: {
                    ...prevState.comment,
                    [name]: value
                }
            };
        });
    };

    render() {
        const { post, comments } = this.state
        const { loggedInUser } = this.props
        return (
            <div className="b-post-container flex row">

                <img className="b-post-img" src={post.imgUrl}></img>
                <div className="b-post-body">
                    <div className="b-post-header">
                        <img className="b-post-header-owner-img" src={post.owner.imgUrl}></img>
                        <span className="b-post-header-owner-nickname">{post.owner.nickName}</span>
                    </div>

                    <section className="b-post-comments-container">
                        <div className="b-post-comments-wrapper">
                            <div className="b-post-comments"> </div>
                            <ul className="b-post-comments-list">
                                {comments.map((comment, i) => (
                                    <li className="b-post-comment-item-container flex row" key={i}>
                                        <div className="b-post-comment-user-img-container">
                                            <img className="b-post-comment-user-img" alt="commenter pic" src={loggedInUser.imgUrl}></img>

                                        </div>
                                        <div className="b-post-comment-txt">
                                            <span className="b-post-comment-user-nickname">{loggedInUser.username}</span>
                                            {comment}
                                        </div>
                                    </li>
                                ))}
                            </ul>
                        </div>
                        <form className="b-post-comment-form" onSubmit={this.sendComment}>
                            <input className="b-post-comment-input" type="text" value={this.state.comment.txt} onChange={this.commentHandleChange} name="txt" />
                            <button className="b-comment-form-submit-btn">Send</button>
                        </form>
                    </section>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        users: state.user.users,
        loggedInUser: state.user.loggedInUser,
        // selectedPost: state.post.selectedPost,
        comments: state.comment.comments
    };
};
const mapDispatchToProps = {
    loadUsers,
    loadPosts, loadPost, likePost, addPost, removePost,
    addComment, loadComments
};

export default connect(mapStateToProps, mapDispatchToProps)(PostPage);


