// React/Redux
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
// import { loadComments, addComment } from '../actions/CommentActions.js';
import { loadUsers } from '../actions/UserActions.js';
import { loadPosts, likePost, addPost, removePost } from '../actions/PostActions.js';

// Style:
import '../../src/assets/css/index/index.css';
import '../../src/assets/css/Home/Home.css';

// Components
import Feed from '../cmps/Feed'
import PostAdd from '../cmps/PostAdd'

class Home extends Component {
  componentDidMount() {
    this.props.loadUsers();
    this.props.loadPosts();
    // this.props.loadComments();
  }

  render() {
    const { loggedInUser, history, users, posts } = this.props
    return (
      <div className="home">
        <div className="home-container">
          <Feed loggedInUser={loggedInUser} history={history} comments={this.props.comments} removePost={this.props.removePost} user={this.props.user} likePost={this.props.likePost} posts={this.props.posts} />

          {this.props.users && loggedInUser &&
            <section className="extras-container">

              <div className="user-image-container">
                <Link to={location => ({
                  ...location,
                  pathname: `/user/logged/${loggedInUser._id}`,
                  state: {
                    loggedInUser: loggedInUser,
                    posts: posts,
                    users: users
                  }
                })}>
                  <img className="user-image" alt="user page" src={loggedInUser.imgUrl}></img>
                </Link>

                <span className="loggedin-user-nickname">{loggedInUser.username}</span>
              </div>
              <div className="extras">
                <PostAdd addPost={this.props.addPost} loggedInUser={loggedInUser} props={this.props} />
              </div>
            </section>}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    // comments: state.comment.comments,
    users: state.user.users,
    loggedInUser: state.user.loggedInUser,
    posts: state.post.posts
  };
};
const mapDispatchToProps = {
  loadUsers,
  loadPosts, likePost, addPost, removePost,
  // addComment, loadComments
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);
