import React, { Component } from 'react'
import './css/FriendsFilter.css'

export default class FriendsFilter extends Component {

    state = {
        filterBy: {
            txt: ''
        }
    }
    onFilterBy = (ev) => {
        const value = ev.target.value;
        if (value === "" || value === ' ') {
            this.setState({ filterBy: { txt: value } })
            this.props.filterFriends({ txt: 'dsgadgad' })

            document.querySelector('.friends-container').classList.add('hidden')
        }
        else {
            this.setState({ filterBy: { txt: value } }, () => this.props.filterFriends(this.state.filterBy))
            document.querySelector('.friends-container').classList.remove('hidden')
        }
    }
    onBlur = (blur) => {
        if (blur) {
            this.setState({ filterBy: { txt: '' } })
            document.querySelector('.friends-container').classList.add('hidden')
        }
    }
    render() {
        return <input className="search-input" autoComplete="off" type="text"
            name="txt" placeholder="find..." value={this.state.filterBy.txt}
            onChange={this.onFilterBy}
            onBlur={() => this.onBlur('blur')}
            onKeyDown={this.onFilterBy}
        ></input>
    }
}