import React, { Component } from 'react';
import '../assets/css/FriendList/FriendList.css'
// import { Link } from 'react-router-dom';
import FriendPreview from '../cmps/FriendPreview'

export default class FriendList extends Component {

    render() {
        const { friends, onSelectFriend } = this.props;

        return <div>
            {friends && <ul className="friends-list flex col">{friends.map((friend) =>
                <FriendPreview key={friend._id} friend={friend} onSelectFriend={() => onSelectFriend(friend)} />
            )} </ul>}
        </div>
    }
}