import HttpService from './HttpService';


export default {
  add,
  query,
  remove
};

function query() {
  // return HttpService.get('comment');  //TODO fix - throwing error
}

function remove(commentId) {
  return HttpService.delete(`comment/${commentId}`);
}
async function add(commentObj, postId) {
  try {
    const addedComment = await HttpService.post(`post/${postId}/comments`,
      { commentObj, postId }
    );
    return addedComment
  } catch (err) {
    console.log(err)
  }
}
