import React, { Component } from 'react';
import '../assets/css/UserPage/userPage.css'
import { connect } from 'react-redux';
import '../assets/css/LoggedinUserPage/loggedinUserPage.css'
import UserActions, { loadUsers, loadUser } from '../actions/UserActions';
import { loadPosts } from '../actions/PostActions';
import { loadFriends, loadSelectedFriend } from '../actions/FriendActions.js';

class UserPage extends Component {

    state = {
        postsCount: 0,
        followersCount: 0,
        posts: [],
        users: [],
        user: {},
        selectedFriend: '',
        // friends: []
    }


    componentDidMount() {
        // this.props.loadFriends();
        // if (this.props.posts) this.setState({ posts: this.props.posts });
        this.onLoadUser()
        this.props.loadPosts()
        this.setState({ posts: this.props.posts });
        this.countPosts(this.state.posts)
        this.countFollowers(this.props.users, this.state.user)
    }

    onLoadUser = async () => {
        const id = this.props.match.params.id
        const foundUser = await this.props.loadUser(id);
        this.setState({ user: foundUser });
        console.log(this.state.user);
    }

    countPosts = (posts) => {
        console.log('posts: ', posts)
        // this.setState(prevState => {
        //     return {
        //         ...prevState,
        //         posts: posts
        //     };
        // });
        let postsCount = 0
        // posts.forEach((post) => {
        //     if (post.owner._id === this.state.user._id) {
        //         postsCount++
        //     }
        // });
        let result = ''
        if (postsCount > 1) {
            result = postsCount + ' posts';
        } else if (postsCount === 0) { result = 'no posts' }
        else { result = '1 post'; }
        // this.setState({ postsCount: result })
        this.setState(prevState => {
            return {
                ...prevState,
                postsCount: result
            };
        });
    }
    countFollowers = (users, selectedFriend) => {
        let followersCount = 0
        users.forEach((user) => {
            if (user.following) {
                user.following.forEach((followedUser) => {
                    if (selectedFriend._id === followedUser._id) {
                        followersCount++
                    }
                })
            }
        });
        let result = ''
        if (followersCount > 1) {
            result = followersCount + ' followers';
        } else if (followersCount === 0) { result = 'no followers' }
        else { result = '1 follower'; }
        // this.setState({ followersCount: result })

        this.setState(prevState => {
            return {
                ...prevState,
                user: selectedFriend
            };
        });

        this.setState(prevState => {
            return {
                ...prevState,
                followersCount: result
            };
        });
    }

    render() {
        const { user } = this.state
        return (<div className="user-page-container">

            <div className="user-info flex col">
                <div className="image-username-container flex row">
                    <img className="user-img" alt="user pic" src={user.imgUrl}></img>
                    <div className="user-socual-info-container flex col">
                        <span className="username">{user.username}</span>
                        <div className="socual-info-section flex row">
                            <span className="user-info-field username">{this.state.postsCount}</span>
                            <span className="user-info-field username">{this.state.followersCount}</span>
                            <span className="user-info-field username">folowing: </span>
                        </div>
                    </div>
                </div>

            </div>

        </div>
        )







        // (<div className="user-page-container">
        //     user...
        //     <span className="-user-nickname">{user.nickName}</span>
        // </div>)
    }
}

const mapStateToProps = state => {
    return {
        posts: state.post.posts,
        friends: state.friend.friends,
        selectedFriend: state.friend.selectedFriend,
        users: state.user.users,
        user: state.user.user

    };
};
const mapDispatchToProps = {
    loadPosts,
    loadFriends, loadSelectedFriend,
    loadUsers, loadUser
};

export default connect(mapStateToProps, mapDispatchToProps)(UserPage);